import { StoreOptions } from "vuex";

// actions 调用mutations 支持异步

// mutations 更新变量
export default {
  namespaced: true,
  state: () => ({
    username: "nouser",
  }),
  actions: {
    getuserinfo({ commit }, payload) {
      //从远程获取登录信息（服务器？）
      commit("updatauser", payload);
    },
  },
  mutations: {
    updatauser(state, payload) {
      state.username = payload;
    },
  },
} as StoreOptions<any>;
