<template>
  <div>
    <el-menu mode="horizontal">
      <div style="padding: 10px">
        <p style="font-size: 20px; font-weight: 900; margin: 10px">WAOJ</p>
      </div>
      <el-menu-item index="1">
        <router-link to="/">Home</router-link>
      </el-menu-item>
      <el-menu-item index="2">
        <router-link to="/problem">Problem</router-link>
      </el-menu-item>
      <el-menu-item index="3">
        <router-link to="/status">Status</router-link>
      </el-menu-item>
      <!-- 控制组件渲染 来展示登录的用户 -->
      <div class="log" v-if="store.state.user.username == 'nouser'">
        <el-button type="primary" plain @click="chlicklogin">Login</el-button>
        <el-button type="primary" plain>Register</el-button>
      </div>
      <div class="log" v-else>
        <p style="font-size: 23px">
          {{ store.state.user.username }}
        </p>
        <el-button
          type="primary"
          plain
          style="margin-left: 8px"
          @click="chlicklogout"
          >Logout</el-button
        >
      </div>
    </el-menu>
  </div>
  <el-dialog
    v-model="dialogTableVisible"
    title="LOGIN"
    center
    width="30%"
    v-if="store.state.user.username == 'nouser'"
  >
    <LoGin />
  </el-dialog>
  <router-view />
</template>
<script lang="ts" setup>
import { useStore } from "vuex";
import { ref, onMounted } from "vue";
import LoGin from "./components/loginpage/LoGin.vue";
const dialogTableVisible = ref(false);
const store = useStore(); //访问全局变量
onMounted(() => {
  //组件挂载完后执行
  let info = localStorage.getItem("info");
  let userinfo = JSON.parse(info as string);
  if (userinfo == null) return;
  console.log(userinfo.islogin);
  if (userinfo.islogin) store.dispatch("user/getuserinfo", userinfo.username);
});
const chlicklogin = () => {
  //显示一个上浮的登录窗口
  // store.dispatch("user/getuserinfo", "wrong answer");
  // console.log(store.state.user);
  console.log("click buttom login");
  dialogTableVisible.value = true;
};
const chlicklogout = () => {
  localStorage.clear();
  store.dispatch("user/getuserinfo", "nouser");
};
// console.log(store.state.user.username);
</script>
