<template>
  <div id="breakgd">
    <el-row :gutter="20">
      <el-col :span="4"><p>用户名</p></el-col>
      <el-col :span="20"
        ><el-input
          class="input"
          v-model="accout"
          placeholder="Please input accout"
      /></el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="4"><p>密码</p></el-col>
      <el-col :span="20"
        ><el-input
          class="input"
          v-model="password"
          placeholder="Please input password"
      /></el-col>
    </el-row>
  </div>
  <el-button
    type="primary"
    @click="chlicklogin"
    style="display: block; margin: 0 auto"
    >Login</el-button
  >
</template>
<script setup lang="ts">
import { ref } from "vue";
import axios from "axios";
import { useStore } from "vuex";
const accout = ref();
const password = ref();
const store = useStore();
const chlicklogin = () => {
  //获取用户名和密码,请求服务区器登录
  console.log(accout.value);
  console.log(password.value);
  // axios.defaults.baseURL = "/proxy_url";
  axios
    .get(
      "http://127.0.0.1:8001/wronganswer_app/login/?account=" +
        accout.value +
        "&password=" +
        password.value
    )
    .then((response) => {
      console.log(response.data);
      if (response.data.state === "OK") {
        store.dispatch("user/getuserinfo", accout.value);
        localStorage.setItem(
          "info",
          JSON.stringify({ islogin: true, username: accout.value })
        );
      } else {
        accout.value = "";
        // 登录成功则清空内容;
        password.value = "";
      }
    })
    .catch((error) => {
      console.log(error);
    });
};
</script>
<style>
.input {
  margin: 10px;
  padding: 0px;
}
#breakgd {
  padding: 10px;
}
</style>
